.App {
  text-align: left;
}

#app-header {
  text-align: center;
  color: black;
}

div {
    color: #fff;
    font-family: Tahoma, Verdana, Segoe, sans-serif;
    padding: 10px;
}

.flex-container {
    display:flex;
}

.fixed {
    width: 100px;
}
.flex-item{
    flex-grow: 1;
}

.warehouse-name {
  margin: 20px;
}

.warehouse-container, .warehouse-contents-container {
    background-color:#2E4272;
}

.level-header {
    background-color:#4F628E;
}

.level-contents-container {
  background-color:#7887AB;
}

.location-container {
    float: left;
    width: 260px;
}

.location-contents-container {
  background-color: #ffb84d;
}

.location-header {
  background-color: #cc7a00;
  height: 50px;
}

.product-container {
  background-color: green;
}

.product-name {
  margin: 0px 0px;
}

.product-details {
  padding: 0px 0px;
}
